import CONF from './conf';

let Util = {
    
    /**
     * 
     */
    redirectToLogin: function () {

        let targetUrl = window.location.href;

        window.location.href = CONF.PASSPORT.AUTH_URL + "/login?service=" +
            encodeURIComponent(CONF.PASSPORT.IDSAUTH_SERVER_URL + "?targetUrl=" +
                encodeURIComponent(targetUrl)) + "&loginTheme=wap_new";
    },

    /**
     * 
     * @param {function} successFn - 
     * @return null
     */
    auth: function (successFn) {

        window.passport_config = CONF.PASSPORT.CONFIG;

        $.probeAuthStatus(function () {

            successFn();
        }, function () {
            Util.redirectToLogin();
        }, CONF.PASSPORT.CONFIG);
    },
    
    /**
     * 
     */
    getStringTime(time) {
        
        let date = time.split(' '),
            YMD = date[0].split('-'),
            HMS = date[1].split(':'),
            Y = YMD[0],
            M = YMD[1],
            D = YMD[2],
            h = HMS[0],
            m = HMS[1],
            s = HMS[2];
        
        return {Y, M, D, h, m, s}
    },
    
    /**
     * 
     */
    formatDate(date, connector) {
        
        return date.getFullYear() + connector + (date.getMonth() + 1) + connector + date.getDate();
    },
    
    /**
     * 
     */
    getErrorCodeTips: function (code) {
        
        let tips = "";
        
        switch (code) {
            case "0001":
                tips = "";
                break;
            case "0002":
                tips = "";
                break;
            case "4001":
                tips = "";
                break;
            case "4002":
                tips = "";
                break;
            case "4011":
                tips = "";
                break;
            case "4013":
                tips = "";
                break;
            case "4015":
                tips = "";
                break;
            case "4016":
                tips = "";
                break;
            case "4017":
                tips = "";
                break;
            case "4018":
                tips = "";
                break;
            case "4019":
                tips = "";
                break;
            case "4020":
                tips = "";
                break;
            case "4021":
                tips = "";
                break;
            case "4023":
                tips = "";
                break;
            case "4043":
                tips = "";
                break;
            case "4031":
                tips = "";
                break;
            case "4032":
                tips = "";
                break;
            case "4033":
                tips = "";
                break;
            case "4034":
                tips = "";
                break;
            case "4024":
                tips = "";
                break;
        }
        
        return tips;
    },

};
export {Util};





